import {
    Component,
    Element,
    Event,
    EventEmitter,
    h,
    Prop,
    // State,
    Watch,
} from '@stencil/core';
import {
    GridStack,
    GridStackElement,
    GridStackNode,
    GridStackWidget,
} from 'gridstack';
import gridstackCss from 'gridstack/dist/gridstack.min.css';

export interface CustomGridStackItem extends GridStackNode {
    id: string;
    x?: number;
    y?: number;
    w?: number;
    h?: number;
    [key: string]: any;
}

@Component({
    tag: 'at-dashboard',
    shadow: false,
})
export class AtDashboard {
    @Element() el: HTMLElement;

    @Prop() widget_templates: CustomGridStackItem[] = [];

    // @Event() changedDashboard: EventEmitter<CustomGridStackItem[]>;
    @Event() changedItem: EventEmitter<CustomGridStackItem>;
    @Event() removedItem: EventEmitter<CustomGridStackItem>;
    @Event() resizeEvent: EventEmitter<GridStackNode>;

    // @State() dashboard: CustomGridStackItem[] = [];

    @Watch('widget_templates')
    widgetTemplatesChanged() {
        // this.updateDashboard();
        this.needsLayout = true;
    }

    private grid?: GridStack;
    private gridContainerRef?: HTMLElement;
    private needsLayout = false;

    componentWillLoad() {
        this.injectStyle();
        // this.updateDashboard();
    }

    componentDidLoad() {
        if (!this.gridContainerRef) return;
        this.grid = GridStack.init(
            {
                margin: 4,
                minRow: 1,
                maxRow: 100,
                float: true,
                columnOpts: {
                    columnMax: 16,
                    breakpoints: [
                        {
                            w: 768,
                            c: 1,
                        },
                    ],
                },
            },
            this.gridContainerRef,
        );

        this.grid.on('change', (_event: Event, items?: GridStackNode[]) => {
            items?.forEach((item) => {
                const dashboardItem = this.widget_templates.find(
                    (d) => d.id === item.id,
                );
                if (dashboardItem) {
                    Object.assign(dashboardItem, {
                        x: item.x,
                        y: item.y,
                        w: item.w,
                        h: item.h,
                    });
                    this.changedItem.emit(dashboardItem);
                }
            });
        });

        this.grid.on('resizestop', (_event: Event, el: GridStackElement) => {
            const node = (el as any).gridstackNode as GridStackNode;
            this.resizeEvent.emit(node);
        });
    }

    componentDidRender() {
        if (!this.grid || !this.needsLayout) return;
        this.grid.removeAll(false);
        this.layoutWidgets();
        this.needsLayout = false;
    }

    disconnectedCallback() {
        this.grid?.destroy(false);
    }

    // private updateDashboard() {
    //     if (this.widget_templates?.length) {
    //         this.dashboard = this.widget_templates.map((template) => ({
    //             ...template,
    //         }));
    //         this.changedDashboard.emit(this.dashboard);
    //     }
    // }

    private layoutWidgets() {
        if (!this.grid) return;
        this.widget_templates.forEach((widget) => this.makeWidget(widget));
    }

    private makeWidget(widget: GridStackWidget) {
        const elSelector = `#${widget.id}`;
        const options: GridStackWidget = {
            id: widget.id,
            x: widget.x,
            y: widget.y,
            w: widget.w,
            h: widget.h,
        };
        this.grid.makeWidget(elSelector, options);
    }

    private removeWidget(widget: CustomGridStackItem): void {
        this.removedItem.emit(widget);
    }

    private injectStyle() {
        const style = document.createElement('style');
        style.textContent = gridstackCss;
        this.el.appendChild(style);
    }

    render() {
        return (
            <div
                class="grid-stack"
                ref={(el) => (this.gridContainerRef = el)}
            >
                {this.widget_templates.map((widget) => (
                    <div
                        class="grid-stack-item"
                        id={widget.id}
                    >
                        <div class="grid-stack-item-content">
                            <div class="absolute top-0 right-0">
                                <at-menu>
                                    <at-button
                                        slot="menu-trigger"
                                        icon="more_vert"
                                        type="secondaryText"
                                    ></at-button>
                                    <at-button
                                        label="Delete"
                                        type="secondaryText"
                                        onAtuiClick={() => {
                                            this.removeWidget(widget);
                                        }}
                                    ></at-button>
                                </at-menu>
                            </div>
                            <slot name={widget.id}></slot>
                        </div>
                    </div>
                ))}
            </div>
        );
    }
}
